body {
    font-family: 'nanum';
    font-weight: 600;
    color: #333;
}
a:link {
    color: #333;
    text-decoration: none; }
a:visited {
    color: inherit;
    text-decoration: none !important;
}
a:hover {
    color: red;
}

.header {
    background-color: white;
    font-size: 20px;
    color: #333;
    display: flex;
    justify-content: space-between;
    height: 50px;
    border-bottom: 1px solid #c1c1c1;
}
.header__elem1 {
    padding-left: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.header__elem1__logo:first-child {
    padding-right: 10px;
    box-sizing: border-box;
    border-right: 1px solid #c1c1c1;
}
.header__elem1__link {
    padding: 10px;
    border-bottom: 2px solid white;
}
.header__elem1__link.--selected {
    color: red !important;
    border-bottom: 2px solid red;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}
.header__elem2 {
    font-size: 15px;
    font-style: italic;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}