@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;900&display=swap');
@font-face {
    font-family: 'nanum';
    src : url("../fonts/NanumGothic.otf")
}

#quick {
    /*background-color: #2e3140;*/
    background: #fbfbfe;
    min-width: 90%;
    min-height: 800px;
}

.wrapper {
    display: flex;
    padding: 10px;
}
.row {
    display: flex;
}
.row:first-child {
    border-top: 1px solid #80808085;

}

.row > div {
    padding: 9px 10px 6px;
}

.attribute {
    background: grey !important;
    color: whitesmoke;
}
.cell {
    border-bottom: 1px solid #80808085;
    border-right: 1px solid #80808085;
    background: whitesmoke;
}
.cell:first-child {
    border-left: 1px solid #80808085;
}
.lowest-tail {
    display: flex;
    justify-content: space-between;
    /* padding: 0px 10px; */
    box-sizing: border-box;
    margin-top: 10px;
    color: #333;
}
.honey-bee {
    background: url(../images/bee.png) no-repeat center center;
    width: 250px;
    height: 200px;
    float: right;
    top: 70px;
    left: 350px;
    position: absolute;
    opacity: 25%;
}
.--flex {
    display: flex;
}
.--relative {
    position: relative;
}
.--inner-eye {
    float: right;
    position: absolute;
    color: #333;
    font-size: 20px;
    margin: 7px 0 0 10px;
}
.--space-between {
    width: 100%;
    display: flex;
    justify-content: space-between;
    border-top: 1px solid whitesmoke;
    padding-top: 7px;
    margin-right: 10px;
    margin-top: 1px;
}
.--long {
    width: 280px;
}
.--short {
    width: 100px;
}
.--extra-short {
    width: 50px;
}
.--elipsis {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.--num {
    text-align: right;
}
.--plus {
    color: red;
}
.--minus {
    color: blue;
}
.--shown-true {
    display: flex;
}
.--shown-false {
    height: 5px;
}
.--simpleInput {
    padding: 7px 10px 5px 40px;
    box-sizing: border-box;
    font-weight: 600;
    font-family: 'nanum';
    font-size: 16px;
}
.--red .cell {
    background: #ff8d8d;
}

.icon {
    margin-right: 5px;
}
.--green {
    transition: .1s color;
    color: #00b700;
}
.--white {
    transition: 1.5s color;
    color: white;
}
.--temp {
    margin-right: 10px;
}
.--black {
    transition: 1.5s color;
    color : #333;
}

