body {
    font-family: 'nanum';
    font-weight: 600;
    color: #333;
}
a:link {
    color: #333;
    text-decoration: none; }
a:visited {
    color: inherit;
    text-decoration: none !important;
}
a:hover {
    color: red;
}

.header {
    background-color: white;
    font-size: 20px;
    color: #333;
    display: flex;
    justify-content: space-between;
    height: 50px;
    border-bottom: 1px solid #c1c1c1;
}
.header__elem1 {
    padding-left: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.header__elem1__logo:first-child {
    padding-right: 10px;
    box-sizing: border-box;
    border-right: 1px solid #c1c1c1;
}
.header__elem1__link {
    padding: 10px;
    border-bottom: 2px solid white;
}
.header__elem1__link.--selected {
    color: red !important;
    border-bottom: 2px solid red;
    box-sizing: border-box;
}
.header__elem2 {
    font-size: 15px;
    font-style: italic;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

#footer {
    background-color: #19171c;
    padding: 40px 0 100px 0;
}

@font-face {
    font-family: 'nanum';
    src : url(/static/media/NanumGothic.755c5696.otf)
}

#quick {
    /*background-color: #2e3140;*/
    background: #fbfbfe;
    min-width: 90%;
    min-height: 800px;
}

.wrapper {
    display: flex;
    padding: 10px;
}
.row {
    display: flex;
}
.row:first-child {
    border-top: 1px solid #80808085;

}

.row > div {
    padding: 9px 10px 6px;
}

.attribute {
    background: grey !important;
    color: whitesmoke;
}
.cell {
    border-bottom: 1px solid #80808085;
    border-right: 1px solid #80808085;
    background: whitesmoke;
}
.cell:first-child {
    border-left: 1px solid #80808085;
}
.lowest-tail {
    display: flex;
    justify-content: space-between;
    /* padding: 0px 10px; */
    box-sizing: border-box;
    margin-top: 10px;
    color: #333;
}
.honey-bee {
    background: url(/static/media/bee.13fdb4f8.png) no-repeat center center;
    width: 250px;
    height: 200px;
    float: right;
    top: 70px;
    left: 350px;
    position: absolute;
    opacity: 25%;
}
.--flex {
    display: flex;
}
.--relative {
    position: relative;
}
.--inner-eye {
    float: right;
    position: absolute;
    color: #333;
    font-size: 20px;
    margin: 7px 0 0 10px;
}
.--space-between {
    width: 100%;
    display: flex;
    justify-content: space-between;
    border-top: 1px solid whitesmoke;
    padding-top: 7px;
    margin-right: 10px;
    margin-top: 1px;
}
.--long {
    width: 280px;
}
.--short {
    width: 100px;
}
.--extra-short {
    width: 50px;
}
.--elipsis {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.--num {
    text-align: right;
}
.--plus {
    color: red;
}
.--minus {
    color: blue;
}
.--shown-true {
    display: flex;
}
.--shown-false {
    height: 5px;
}
.--simpleInput {
    padding: 7px 10px 5px 40px;
    box-sizing: border-box;
    font-weight: 600;
    font-family: 'nanum';
    font-size: 16px;
}
.--red .cell {
    background: #ff8d8d;
}

.icon {
    margin-right: 5px;
}
.--green {
    transition: .1s color;
    color: #00b700;
}
.--white {
    transition: 1.5s color;
    color: white;
}
.--temp {
    margin-right: 10px;
}
.--black {
    transition: 1.5s color;
    color : #333;
}

