    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    color: #333;
    font-size: 15px;
}

.fas-wrapper {
    height: 100%;
    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    color: #333;
    font-size: 15px;
}
