.layer-a {

}
.layer-a .head {
    display: flex;
    border: 1px solid grey;
    padding: 10px;
    box-sizing: border-box;
    color: #333;
    font-weight: 600;
    font-size: 18px;
    justify-content: space-between;
}
.layer-a .head > div {
    min-width: 50px;
}
.layer-a.--showDetail .layer-b {
    display: block;
}
.layer-a .layer-b {
    display: none;
}